@import url("https://fonts.googleapis.com/css2?family=Bangers&family=Lobster&family=Roboto+Mono:wght@500&family=Roboto:wght@500&family=Russo+One&family=Varta&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Bangers&family=Lobster&family=Odibee+Sans&family=Roboto+Mono:wght@500&family=Roboto:wght@500&family=Russo+One&family=Varta&display=swap");

*,
*::after,
*::before {
  /* border: 1px dashed; */
  box-sizing: border-box;
}
#canvas-container {
  display: grid;
  width: 800px;
  grid-template-columns: 1fr 10fr;
  justify-content: center;
  align-items: center;
  margin: 0 auto;
}
#game-canvas {
  border: solid 3px gray;
  background-color: lightgray;
  cursor: pointer;

  -webkit-box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.5);
  -box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.5);
}

.touch-drag-test {
  height: 100px;
  width: 100px;
  background-color: blue;
}
.dropBox {
  height: 150px;
  width: 150px;
  background-color: aqua;
  display: flex;
  justify-content: center;
  align-items: center;
}
.control-box {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
  width: 700px;
  margin-top: 0.2rem;
  margin-left: auto;
  margin-right: auto;
  padding-left: 0.3rem;
  padding-right: 0.3rem;
  background-color: rgb(248, 233, 214);
  -box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.5);
  -webkit-box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.5);
  -moz-box-shadow: 4px 4px 8px rgba(0, 0, 0, 0.5);
}
.control-side-panel {
  /* height: 100px; */

  text-align: left;
}
.control-panel-container {
  display: grid;

  grid-template-rows: 1fr 2fr 2fr 2fr 2fr;
}
.control-panel-display {
  height: 40px;
}
.floatingModal {
  position: absolute;
  width: 100px;
  padding: 0.3rem;

  background-color: rgb(233, 220, 220);
}
