*,
*::after,
*::before {
  box-sizing: border-box;
  /* border: 1px dashed black; */
}
body {
  margin: 0;
  padding: 0;
  font-family: "Roboto Mono", monospace;
  height: 100vh;
}
.body-area {
  display: grid;
  justify-content: center;
  align-items: center;
  margin-top: 10px;
}
.form-container {
  width: 500px;
  margin: 0 auto;
  padding-left: 10px;
  padding-right: 10px;
  background-color: #e99516;
  border-radius: 5px;
  border: 1px dashed rgb(185, 28, 0);
}
.title {
  width: 500px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.5rem;
  padding: 10px;
  border-radius: 5px;
}
.title,
.result {
  background-color: #255784;
  color: white;
}

.theory {
  display: grid;
}
.lab label,
.theory label {
  padding-top: 10px;
  padding-bottom: 10px;
  font-size: 1.2rem;
  text-align: center;
}

.theory input {
  font-size: 1.3rem;
  border-radius: 5px;
  border-radius: 5px;
  border: 1px dashed rgb(185, 28, 0);
}

.show {
  padding: 10px;
  border: 1px dashed orange;
  border-radius: 5px;
  font-size: 1.4rem;
  font-weight: bold;
  text-align: center;
  background-color: white;
  color: black;
}
.buttons {
  display: flex;
  justify-content: space-between;
}
.w3-btn {
  font-size: 1.2rem;

  margin-top: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  background-color: #255784;
  color: white;
}
input[type="text"]:focus {
  background-color: rgb(121, 186, 207);
}
